import React, { useState } from 'react';
import { Outlet, useNavigate, useLocation } from 'react-router-dom';
import { Database } from 'lucide-react';
import { useAuth } from '../contexts/AuthContext';
import { useTheme } from '../contexts/ThemeContext';
import { useModal } from '../contexts/ModalContext';
import Logo from './Logo';
import CreateInspection from '../pages/CreateInspection';
import './Layout.css';

const Layout: React.FC = () => {
  const { user, logout } = useAuth();
  const { theme, setTheme } = useTheme();
  const { isCreateModalOpen, openCreateModal, closeCreateModal } = useModal();
  
  console.log('Layout rendered, isCreateModalOpen:', isCreateModalOpen);
  const navigate = useNavigate();
  const location = useLocation();
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  const canGoBack = location.pathname !== '/' && 
                   location.pathname !== '/inspections' && 
                   location.pathname !== '/login' && 
                   location.pathname !== '/register';

  return (
    <div className={`layout theme-${theme}`}>
      {/* Sidebar */}
      <aside className={`sidebar ${isSidebarOpen ? 'open' : 'closed'}`}>
        <div className="sidebar-header">
          <Logo />
          <button 
            className="sidebar-toggle"
            onClick={() => setIsSidebarOpen(!isSidebarOpen)}
          >
            {isSidebarOpen ? '' : ''}
          </button>
        </div>

        <nav className="sidebar-nav">
          <button
            className={`nav-item ${location.pathname === '/' || location.pathname === '/inspections' ? 'active' : ''}`}
            onClick={() => navigate('/inspections')}
          >
             
          </button>
          
          <button
            className="nav-item"
            onClick={() => {
              console.log('Button clicked, opening modal...');
              openCreateModal();
            }}
          >
              
          </button>
          
          <button
            className="nav-item"
            onClick={() => navigate('/analytics')}
          >
             
          </button>
          
          <button
            className="nav-item"
            onClick={() => navigate('/users')}
          >
             
          </button>
          
          <button
            className="nav-item"
            onClick={() => navigate('/settings')}
          >
             
          </button>
          
          <button
            className="nav-item nav-item-with-icons"
            onClick={() => window.open('https://cmsauto.ru/#/registry', '_blank')}
          >
            <Database size={16} />
            <span>CMS AUTO</span>
          </button>
          
          {canGoBack && (
            <button
              className="nav-item back-button"
              onClick={() => navigate(-1)}
            >
               
            </button>
          )}
        </nav>

        <div className="sidebar-footer">
          <div className="user-info">
            <span> {user?.fullName}</span>
          </div>
          <div className="theme-selector">
            <label className="theme-label">:</label>
            <select 
              className="theme-select"
              value={theme}
              onChange={(e) => setTheme(e.target.value as any)}
            >
              <option value="light"></option>
              <option value="dark"></option>
              <option value="windows97">Windows 97</option>
              <option value="windowsXP">Windows XP</option>
              <option value="ios">iOS</option>
              <option value="matrix">Matrix</option>
              <option value="nokia3110">Nokia 3110</option>
            </select>
          </div>
          <button className="logout-button" onClick={handleLogout}>
             
          </button>
        </div>
      </aside>

      {/* Main content */}
      <main className="main-content">
        <Outlet />
      </main>

      {/*     */}
      <CreateInspection 
        isOpen={isCreateModalOpen}
        onClose={closeCreateModal}
      />
    </div>
  );
};

export default Layout;