          category: contextInspection.property_type,
          type: obj.type || ' ',
          make: obj.make || '',
          model: obj.model || '',
          vin: obj.vin || '',
          registration_number: obj.license_plate || '',
          year: obj.year,
          color: obj.color,
          photos_count: 0
