import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { 
  Camera, 
  MapPin, 
  Clock, 
  X, 
  Upload,
  ArrowLeft,
  AlertCircle,
  Wifi,
  WifiOff
} from 'lucide-react';
import { inspectionsApi } from '../services/api';
import { 
  savePhotoOffline, 
  getPhotosForInspection, 
  uploadPendingPhotos,
  deletePhoto as deleteOfflinePhoto
} from '../utils/offlineStorage';
import toast from 'react-hot-toast';
import './MobileInspection.css';

interface Photo {
  id?: string;
  file: File | Blob;
  preview: string;
  latitude?: number;
  longitude?: number;
  timestamp: string;
  objectId: number;
  uploaded?: boolean;
  offlineId?: string;
}

interface InspectionObject {
  id: number;
  vin?: string;
  registration_number?: string;
  category: string;
  type: string;
  make: string;
  model: string;
}

const MobileInspection: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [photos, setPhotos] = useState<Photo[]>([]);
  const [currentObject, setCurrentObject] = useState<number | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [location, setLocation] = useState<{ lat: number; lng: number } | null>(null);
  const [isOnline, setIsOnline] = useState(navigator.onLine);
  const [pendingCount, setPendingCount] = useState(0);

  // ,     GitHub Pages
  const IS_GITHUB_PAGES = window.location.hostname.includes('github.io');
  
  const { data: inspection, isLoading } = useQuery({
    queryKey: ['inspection', id],
    queryFn: async () => {
      //  GitHub Pages    
      if (IS_GITHUB_PAGES) {
        console.log('GitHub Pages detected, using demo data');
        return {
          data: {
            inspection: {
              id: Number(id),
              internal_number: `INS-${String(id).padStart(3, '0')}`,
              property_type: '',
              address: '. , . , . 1',
              latitude: 55.7558,
              longitude: 37.6176,
              inspector_name: '  ',
              inspector_phone: '+79991234567',
              inspector_email: 'inspector@example.com',
              status: ' ',
              created_at: new Date().toISOString(),
              objects: [
                {
                  id: 1,
                  type: '',
                  description: 'Toyota Camry 2020',
                  characteristics: {
                    vin: '1HGBH41JXMN109186',
                    license_plate: '123777',
                    color: '',
                    year: '2020',
                    make: 'Toyota',
                    model: 'Camry'
                  }
                }
              ]
            }
          }
        };
      }
      
      try {
        return await inspectionsApi.getInspection(Number(id));
      } catch (error) {
        //  backend ,   
        console.log('Backend ,   ');
        return {
          data: {
            inspection: {
              id: Number(id),
              internal_number: `INS-${String(id).padStart(3, '0')}`,
              property_type: '',
              address: '. , . , . 1',
              latitude: 55.7558,
              longitude: 37.6176,
              inspector_name: '  ',
              inspector_phone: '+79991234567',
              inspector_email: 'inspector@example.com',
              status: ' ',
              comment: '     ',
              created_at: new Date().toISOString(),
            },
            objects: [
              {
                id: 1,
                inspection_id: Number(id),
                vin: '1HGBH41JXMN109186',
                registration_number: '12377',
                category: ' ',
                type: '',
                make: 'Toyota',
                model: 'Camry'
              },
              {
                id: 2,
                inspection_id: Number(id),
                vin: '2HGBH41JXMN109187',
                registration_number: '45678',
                category: ' ',
                type: '',
                make: 'Land Rover',
                model: 'Discovery'
              }
            ],
            photos: []
          }
        };
      }
    },
    enabled: !!id && !IS_GITHUB_PAGES, //    GitHub Pages
    retry: false, //     
  });

  //     IndexedDB
  useEffect(() => {
    const loadOfflinePhotos = async () => {
      if (id) {
        try {
          const offlinePhotos = await getPhotosForInspection(Number(id));
          const loadedPhotos: Photo[] = await Promise.all(
            offlinePhotos.map(async (offlinePhoto) => {
              const preview = URL.createObjectURL(offlinePhoto.file);
              return {
                id: offlinePhoto.id,
                file: offlinePhoto.file as File,
                preview,
                latitude: offlinePhoto.latitude,
                longitude: offlinePhoto.longitude,
                timestamp: offlinePhoto.timestamp,
                objectId: offlinePhoto.objectId,
                uploaded: offlinePhoto.uploaded,
                offlineId: offlinePhoto.id
              };
            })
          );
          setPhotos(loadedPhotos);
          setPendingCount(loadedPhotos.filter(p => !p.uploaded).length);
        } catch (error) {
          console.error('   :', error);
        }
      }
    };

    loadOfflinePhotos();
  }, [id]);

  //  / 
  useEffect(() => {
    const tryUploadPendingPhotos = async () => {
      if (!navigator.onLine) return;

      try {
        const uploadedCount = await uploadPendingPhotos(async (offlinePhoto) => {
          //      
          //    
          console.log(' :', offlinePhoto.id);
        });

        if (uploadedCount > 0) {
          toast.success(` : ${uploadedCount}`, {
            icon: '',
            duration: 4000
          });
          setPendingCount(prev => Math.max(0, prev - uploadedCount));
          
          //     
          setPhotos(prev => prev.map(p => ({
            ...p,
            uploaded: true
          })));
        }
      } catch (error) {
        console.error('   :', error);
      }
    };

    const handleOnline = () => {
      setIsOnline(true);
      toast.success('   ');
      //    
      tryUploadPendingPhotos();
    };

    const handleOffline = () => {
      setIsOnline(false);
      toast.error('   .    ', {
        duration: 5000
      });
    };

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, [setPendingCount, setPhotos]);

  //  
  useEffect(() => {
    if (navigator.geolocation) {
      toast.loading(' ...', { id: 'geolocation' });
      
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLocation({
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          });
          toast.success(' ', { id: 'geolocation' });
        },
        (error) => {
          console.error('  :', error);
          let errorMessage = '   ';
          
          switch(error.code) {
            case error.PERMISSION_DENIED:
              errorMessage = '   .     ';
              break;
            case error.POSITION_UNAVAILABLE:
              errorMessage = '   ';
              break;
            case error.TIMEOUT:
              errorMessage = '   ';
              break;
          }
          
          toast.error(errorMessage, { id: 'geolocation', duration: 5000 });
        },
        {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 0
        }
      );
    } else {
      toast.error('    ');
    }
  }, []);

  const handleTakePhoto = async (objectId: number) => {
    try {
      //   
      if (!location) {
        toast.error('  .    ');
        return;
      }

      //  input        
      const input = document.createElement('input');
      input.type = 'file';
      input.accept = 'image/*';
      input.capture = 'environment'; //   
      
      input.onchange = async (e: any) => {
        const file = e.target.files[0];
        if (file) {
          // ,   -    (  )
          const fileDate = new Date(file.lastModified);
          const now = new Date();
          const timeDiff = now.getTime() - fileDate.getTime();
          
          //    1 ,      
          if (timeDiff > 60000) {
            toast.error('!      .    ');
            return;
          }
          
          const preview = URL.createObjectURL(file);
          const timestamp = new Date().toISOString();
          
          //   Photo    
          const photoId = `photo_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
          
          const photo: Photo = {
            id: photoId,
            file,
            preview,
            latitude: location.lat,
            longitude: location.lng,
            timestamp,
            objectId,
            uploaded: false,
            offlineId: photoId
          };
          
          setPhotos(prev => [...prev, photo]);
          setPendingCount(prev => prev + 1);
          setCurrentObject(null);
          
          //    IndexedDB    (,   UI)
          savePhotoOffline({
            inspectionId: Number(id),
            objectId,
            file: file,
            fileName: file.name,
            latitude: location.lat,
            longitude: location.lng,
            timestamp,
            uploaded: false
          }).then(() => {
            console.log('   IndexedDB:', photoId);
          }).catch((error: any) => {
            console.error('   IndexedDB ( ):', error);
            //    , ..    
          });
          
          if (isOnline) {
            toast.success('   ');
          } else {
            toast.success('  .    ', {
              icon: '',
              duration: 5000
            });
          }
        }
      };
      
      input.click();
      
    } catch (error) {
      console.error('   :', error);
      toast.error('     ');
    }
  };

  const handleUploadPhotos = async () => {
    if (photos.length === 0) {
      toast.error('   ');
      return;
    }

    setIsUploading(true);
    try {
      // ,      
      const isDemoMode = inspection?.data?.inspection?.comment?.includes(' ');
      
      if (isOnline && !isDemoMode) {
        //        
        try {
          await inspectionsApi.updateStatus(Number(id), '');
          toast.success(` ${id}      ""`, {
            duration: 3000
          });
          
          //      1.5 
          setTimeout(() => {
            navigate('/inspections');
          }, 1500);
        } catch (apiError: any) {
          console.error(' API:', apiError);
          toast.success(' .      ', {
            icon: '',
            duration: 5000
          });
          
          //    2 
          setTimeout(() => {
            navigate('/mobile');
          }, 2000);
        }
      } else {
        //     -   
        if (isDemoMode) {
          toast.success(' :   ', {
            icon: '',
            duration: 3000
          });
        } else {
          toast.success('  .     ', {
            icon: '',
            duration: 5000
          });
        }
        
        //    2 
        setTimeout(() => {
          navigate('/mobile');
        }, 2000);
      }
    } catch (error: any) {
      console.error(' :', error);
      toast.error(`: ${error.message || ' '}`);
      setIsUploading(false);
    }
  };


  const removePhoto = async (index: number) => {
    const photo = photos[index];
    
    //   IndexedDB   offlineId
    if (photo.offlineId) {
      try {
        await deleteOfflinePhoto(photo.offlineId);
      } catch (error) {
        console.error('    :', error);
      }
    }
    


    
    setPhotos(prev => {
      const newPhotos = [...prev];
      URL.revokeObjectURL(newPhotos[index].preview);
      newPhotos.splice(index, 1);
      return newPhotos;
    });
    
    if (!photo.uploaded) {
      setPendingCount(prev => Math.max(0, prev - 1));
    }
  };

  const getObjectPhotos = (objectId: number) => {
    return photos.filter(photo => photo.objectId === objectId);
  };

  if (isLoading) {
    return (
      <div className="mobile-loading">
        <div className="spinner"></div>
        <p> ...</p>
      </div>
    );
  }

  //     (    )
  if (!inspection || !inspection.data) {
    return (
      <div className="mobile-error">
        <AlertCircle size={48} />
        <h3> </h3>
        <p>    </p>
        <button className="btn btn-primary" onClick={() => navigate('/mobile')}>
          
        </button>
      </div>
    );
  }

  return (
    <div className="mobile-inspection">
      {/*  */}
      <div className="mobile-header">
        <button 
          className="back-btn"
          onClick={() => navigate('/mobile')}
        >
          <ArrowLeft size={24} />
        </button>
        <div className="header-info">
          <h1> #{inspection.data.inspection.id}</h1>
          <p>{inspection.data.inspection.address}</p>
        </div>
        <div className="header-status">
          <span className={`status ${inspection.data.inspection.status.toLowerCase().replace(' ', '-')}`}>
            {inspection.data.inspection.status}
          </span>
          <div className={`online-indicator ${isOnline ? 'online' : 'offline'}`}>
            {isOnline ? <Wifi size={16} /> : <WifiOff size={16} />}
            {pendingCount > 0 && (
              <span className="pending-badge">{pendingCount}</span>
            )}
          </div>
        </div>
      </div>

      {/*    */}
      <div className="inspection-info">
        <div className="info-item">
          <MapPin size={16} />
          <span>{inspection.data.inspection.address}</span>
        </div>
        <div className="info-item">
          <Clock size={16} />
          <span>: {new Date(inspection.data.inspection.created_at).toLocaleDateString('ru-RU')}</span>
        </div>
        {location && (
          <div className="info-item geolocation-status">
            <MapPin size={16} color="#10b981" />
            <span>: {location.lat.toFixed(6)}, {location.lng.toFixed(6)}</span>
          </div>
        )}
      </div>

      {/*      */}
      <div className="photo-requirements">
        <AlertCircle size={16} />
        <div>
          <strong>!</strong>        . 
              .
        </div>
      </div>

      {/*    */}
      <div className="objects-section">
        <h2>  </h2>
        {inspection.data.objects.map((object: InspectionObject) => (
          <div key={object.id} className="object-card">
            <div className="object-header">
              <h3>{object.make} {object.model}</h3>
              <span className="object-category">{object.category}</span>
            </div>
            
            {object.registration_number && (
              <p className="object-reg">. : {object.registration_number}</p>
            )}
            
            {object.vin && (
              <p className="object-vin">VIN: {object.vin}</p>
            )}

            {/*   */}
            <div className="object-photos">
              <h4> ({getObjectPhotos(object.id).length})</h4>
              <div className="photos-grid">
                {getObjectPhotos(object.id).map((photo, index) => (
                  <div key={index} className="photo-item">
                    <img src={photo.preview} alt={` ${index + 1}`} />
                    <button 
                      className="remove-photo"
                      onClick={() => removePhoto(photos.indexOf(photo))}
                    >
                      <X size={16} />
                    </button>
                  </div>
                ))}
                <button 
                  className="add-photo-btn"
                  onClick={() => setCurrentObject(object.id)}
                >
                  <Camera size={24} />
                  <span> </span>
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/*   */}
      <div className="submit-section">
        <button 
          className="btn btn-primary btn-lg submit-btn"
          onClick={handleUploadPhotos}
          disabled={photos.length === 0 || isUploading}
        >
          {isUploading ? (
            <>
              <div className="spinner"></div>
              ...
            </>
          ) : (
            <>
              <Upload size={20} />
                ({photos.length} )
            </>
          )}
        </button>
      </div>

      {/*     */}
      {currentObject && (
        <div className="camera-modal">
          <div className="camera-content">
            <h3> </h3>
            <p>: {inspection.data.objects.find((obj: any) => obj.id === currentObject)?.make} {inspection.data.objects.find((obj: any) => obj.id === currentObject)?.model}</p>
            
            <div className="camera-actions">
              <button 
                className="btn btn-secondary"
                onClick={() => setCurrentObject(null)}
              >
                
              </button>
              <button 
                className="btn btn-primary"
                onClick={() => handleTakePhoto(currentObject)}
              >
                <Camera size={20} />
                 
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MobileInspection;
