import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { useQuery } from '@tanstack/react-query';
import { useNavigate, useLocation } from 'react-router-dom';
import {
  Download,
  Car,
  Building,
  Home,
  Wrench,
  Plus,
  Filter,
  X,
  Camera
} from 'lucide-react';
import { inspectionsApi } from '../services/api';
import { exportInspectionsToExcel } from '../utils/excelExport';
import { useInspections } from '../contexts/InspectionsContext';
import { useModal } from '../contexts/ModalContext';
import toast from 'react-hot-toast';
import './Inspections.css';

interface Inspection {
  id: number;
  internal_number?: string;
  status: string;
  address: string;
  inspector_name: string;
  recipient_name: string;
  created_at: string;
  sent_at?: string;
  objects_count: number;
  photos_count: number;
  created_by_name: string;
  property_type: string;
  object_type: string;
  object_description: string;
}

interface Filters {
  status: string;
  address: string;
  inspector: string;
  dateFrom: string;
  dateTo: string;
  internalNumber: string;
}

const Inspections: React.FC = () => {
  const [filters, setFilters] = useState<Filters>({
    status: '',
    address: '',
    inspector: '',
    dateFrom: '',
    dateTo: '',
    internalNumber: '',
  });
  const [showFilters, setShowFilters] = useState(false);
  const [page, setPage] = useState(1);
  const navigate = useNavigate();
  const location = useLocation();
  const { updateInspectionsCount, inspections: contextInspections, refreshInspections } = useInspections();
  const { openCreateModal } = useModal();

  //    -
  const getDemoInspections = (): Inspection[] => {
    return [
      {
        id: 1,
        internal_number: '220525-1626',
        status: '',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date().toISOString(),
        sent_at: '2022-05-25T04:00:00Z',
        property_type: '',
        object_type: '',
        object_description: 'Subaru FORESTER',
        photos_count: 25,
        objects_count: 1,
        created_by_name: '',
        address: '. , . , 1'
      },
      {
        id: 2,
        internal_number: '220525-1627',
        status: ' ',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 30).toISOString(), // 30  
        sent_at: '2022-05-25T04:01:00Z',
        property_type: '',
        object_type: '',
        object_description: ' ., . , - 103',
        photos_count: 0,
        objects_count: 1,
        created_by_name: '',
        address: ' ., . , - 103'
      },
      {
        id: 3,
        internal_number: '220525-1628',
        status: '',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 60).toISOString(), // 1  
        property_type: '',
        object_type: '',
        object_description: ' ., . , - 737',
        photos_count: 0,
        objects_count: 1,
        created_by_name: '',
        address: ' ., . , - 737'
      },
      {
        id: 4,
        internal_number: '220525-1629',
        status: '',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 90).toISOString(), // 1.5  
        sent_at: '2022-05-25T04:02:00Z',
        property_type: '',
        object_type: '',
        object_description: ' ., . , - 737',
        photos_count: 36,
        objects_count: 1,
        created_by_name: '',
        address: ' ., . , - 737'
      },
      {
        id: 5,
        internal_number: '220525-1630',
        status: ' ',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 60 * 2).toISOString(), // 2  
        sent_at: '2022-05-25T04:03:00Z',
        property_type: '',
        object_type: '',
        object_description: ' ., . , - 737',
        photos_count: 15,
        objects_count: 1,
        created_by_name: '',
        address: ' ., . , - 737'
      },
      {
        id: 6,
        internal_number: '220525-1631',
        status: '',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 60 * 3).toISOString(), // 3  
        sent_at: '2022-05-25T04:04:00Z',
        property_type: '',
        object_type: '',
        object_description: ' ., . , . , . 80',
        photos_count: 56,
        objects_count: 1,
        created_by_name: '',
        address: ' ., . , . , . 80'
      },
      {
        id: 7,
        internal_number: '220525-1632',
        status: '',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 60 * 4).toISOString(), // 4  
        sent_at: '2022-05-25T04:05:00Z',
        property_type: '',
        object_type: '',
        object_description: ' ., . , . , . 6',
        photos_count: 129,
        objects_count: 1,
        created_by_name: '',
        address: ' ., . , . , . 6'
      },
      {
        id: 8,
        internal_number: '220525-1633',
        status: '',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 60 * 5).toISOString(), // 5  
        sent_at: '2022-05-25T04:06:00Z',
        property_type: '',
        object_type: '',
        object_description: 'SUMITOMO SH135X-3B',
        photos_count: 22,
        objects_count: 1,
        created_by_name: '',
        address: '. , . , 15'
      },
      {
        id: 9,
        internal_number: '220525-1634',
        status: ' ',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 60 * 6).toISOString(), // 6  
        sent_at: '2022-05-25T04:07:00Z',
        property_type: '',
        object_type: '',
        object_description: ' 5453',
        photos_count: 8,
        objects_count: 1,
        created_by_name: '',
        address: '. , . , 8'
      },
      {
        id: 10,
        internal_number: '220525-1635',
        status: '',
        inspector_name: ' ..',
        recipient_name: ' ..',
        created_at: new Date(Date.now() - 1000 * 60 * 60 * 7).toISOString(), // 7  
        sent_at: '2022-05-25T04:08:00Z',
        property_type: '',
        object_type: '',
        object_description: ' 5453',
        photos_count: 22,
        objects_count: 1,
        created_by_name: '',
        address: '. , . , 8'
      }
    ];
  };

  //   -
  const getFilteredInspections = useMemo(() => {
    //  -     
    let allInspections = [...getDemoInspections()];
    
    //        
    if (contextInspections.length > 0) {
      allInspections = [...contextInspections, ...allInspections];
    }
    
    let filtered = allInspections;

    if (filters.status) {
      filtered = filtered.filter(i => i.status === filters.status);
    }
    if (filters.address) {
      filtered = filtered.filter(i => 
        i.address.toLowerCase().includes(filters.address.toLowerCase())
      );
    }
    if (filters.inspector) {
      filtered = filtered.filter(i => 
        i.inspector_name.toLowerCase().includes(filters.inspector.toLowerCase())
      );
    }
    if (filters.internalNumber) {
      filtered = filtered.filter(i => 
        i.internal_number?.includes(filters.internalNumber)
      );
    }
    if (filters.dateFrom) {
      filtered = filtered.filter(i => 
        new Date(i.created_at) >= new Date(filters.dateFrom)
      );
    }
    if (filters.dateTo) {
      filtered = filtered.filter(i => 
        new Date(i.created_at) <= new Date(filters.dateTo)
      );
    }

    return filtered;
  }, [filters, contextInspections]);

  // ,     GitHub Pages
  const IS_GITHUB_PAGES = window.location.hostname.includes('github.io');
  
  const { data, isLoading, error, refetch } = useQuery({
    queryKey: ['inspections', filters, page],
    queryFn: () => inspectionsApi.getInspections({ ...filters, page, limit: 20 }),
    retry: false, //     
    refetchOnWindowFocus: false, //     
    enabled: !IS_GITHUB_PAGES, //    GitHub Pages
  });

  //      
  useEffect(() => {
    const state = location.state as { refresh?: boolean; newInspection?: string } | null;
    if (state?.refresh) {
      //     
      if (state.newInspection) {
        toast.success(` ${state.newInspection}   `, {
          duration: 4000,
          icon: ''
        });
      }
      //  state     
      window.history.replaceState({}, document.title);
      //     (    GitHub Pages)
      if (!IS_GITHUB_PAGES) {
        refetch();
      }
      updateInspectionsCount();
    }
  }, [location.state, refetch, updateInspectionsCount, IS_GITHUB_PAGES]);

  const handleFilterChange = useCallback((key: keyof Filters, value: string) => {
    setFilters(prev => ({ ...prev, [key]: value }));
    setPage(1);
  }, []);

  const clearFilters = () => {
    setFilters({
      status: '',
      address: '',
      inspector: '',
      dateFrom: '',
      dateTo: '',
      internalNumber: '',
    });
    setPage(1);
  };


  const handleExport = async () => {
    const dataToExport = getFilteredInspections;
    if (dataToExport.length === 0) {
      toast.error('   ');
      return;
    }
    
    if (!window.confirm(` ${dataToExport.length}   Excel?`)) {
      return;
    }
    
    try {
      exportInspectionsToExcel(dataToExport, '_');
      toast.success(` : ${dataToExport.length}`);
    } catch (error) {
      console.error(' :', error);
      toast.error(' ');
    }
  };

  const getStatusClass = (status: string) => {
    switch (status) {
      case '': return 'status-ready';
      case ' ': return 'status-in-progress';
      case '': return 'status-checking';
      case '': return 'status-revision';
      case '': return 'status-cancelled';
      default: return 'status';
    }
  };


  const formatDate = (dateString: string) => {
    try {
      const date = new Date(dateString);
      if (isNaN(date.getTime())) {
        return ' ';
      }
      return date.toLocaleDateString('ru-RU', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
      });
    } catch (error) {
      console.error('  :', error);
      return ' ';
    }
  };

  const formatTime = (dateString: string) => {
    try {
      const date = new Date(dateString);
      if (isNaN(date.getTime())) {
        return '--:--';
      }
      return date.toLocaleTimeString('ru-RU', { 
        hour: '2-digit', 
        minute: '2-digit',
        hour12: false
      });
    } catch (error) {
      console.error('  :', error);
      return '--:--';
    }
  };


  const getPropertyTypeIcon = (type: string) => {
    switch (type) {
      case '':
        return <Car size={16} />;
      case '':
        return <Building size={16} />;
      case '':
        return <Home size={16} />;
      default:
        return <Wrench size={16} />;
    }
  };


  if (error) {
    return (
      <div className="error-state">
        <h3>  </h3>
        <p>  </p>
        <button 
          className="btn btn-primary" 
          onClick={() => !IS_GITHUB_PAGES && refetch()}
          disabled={IS_GITHUB_PAGES}
        >
          
        </button>
      </div>
    );
  }

  return (
    <div className="inspections-page">
      <div className="inspections-content">
        <div className="page-header">
        <div className="header-left">
          <h1 className="page-title"></h1>
          <div className="total-count">{getFilteredInspections.length}</div>
        </div>
        
        <div className="header-right">
          <button 
            className="btn btn-secondary btn-sm"
            onClick={() => setShowFilters(!showFilters)}
          >
            <Filter size={16} />
            
          </button>
          
          <button className="btn btn-secondary btn-sm" onClick={handleExport}>
            <Download size={16} />
            
          </button>
          
          <button className="btn btn-primary" onClick={openCreateModal}>
            <Plus size={16} />
             
          </button>
        </div>
      </div>

      {/*    */}
      {showFilters && (
        <div className="filters-panel">
          <div className="filters-grid">
            <div className="form-group">
              <label className="form-label"> </label>
              <input
                type="text"
                className="form-input"
                placeholder=" "
                value={filters.internalNumber}
                onChange={(e) => handleFilterChange('internalNumber', e.target.value)}
              />
            </div>

            <div className="form-group">
              <label className="form-label"> </label>
              <input
                type="text"
                className="form-input"
                placeholder=" "
                value={filters.address}
                onChange={(e) => handleFilterChange('address', e.target.value)}
              />
            </div>

            <div className="form-group">
              <label className="form-label"></label>
              <input
                type="text"
                className="form-input"
                placeholder=" "
                value={filters.inspector}
                onChange={(e) => handleFilterChange('inspector', e.target.value)}
              />
            </div>

            <div className="form-group">
              <label className="form-label"></label>
              <select
                className="form-select"
                value={filters.status}
                onChange={(e) => handleFilterChange('status', e.target.value)}
              >
                <option value=""> </option>
                <option value=" "> </option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>

            <div className="form-group">
              <label className="form-label">  ()</label>
              <input
                type="date"
                className="form-input"
                value={filters.dateFrom}
                onChange={(e) => handleFilterChange('dateFrom', e.target.value)}
              />
            </div>

            <div className="form-group">
              <label className="form-label">  ()</label>
              <input
                type="date"
                className="form-input"
                value={filters.dateTo}
                onChange={(e) => handleFilterChange('dateTo', e.target.value)}
              />
            </div>
          </div>

          <div className="filters-actions">
            <button 
              className="btn btn-secondary"
              onClick={clearFilters}
            >
              <X size={16} />
               
            </button>
          </div>
        </div>
      )}


      {/*   */}
      <div className="table-container">
        {isLoading ? (
          <div className="loading">
            <div className="spinner"></div>
             ...
          </div>
        ) : (
          <>
            <table className="inspections-table">
              <thead>
                <tr>
                  <th> </th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th> </th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                      {(error ? getFilteredInspections : (data?.data?.inspections || getFilteredInspections)).map((inspection: Inspection) => (
                  <tr 
                    key={inspection.id}
                    onClick={() => navigate(`/inspections/${inspection.id}`)}
                    style={{ cursor: 'pointer' }}
                  >
                    <td>
                      <div className="date-time">
                        <div className="date">{formatDate(inspection.created_at)}</div>
                        <div className="time">{formatTime(inspection.created_at)}</div>
                      </div>
                    </td>
                    <td>
                      <span className="inspection-number">
                        {inspection.internal_number || `INS-${String(inspection.id).padStart(3, '0')}`}
                      </span>
                    </td>
                    <td>
                      <span className="inspector-name">{inspection.inspector_name}</span>
                    </td>
                    <td>
                      <span className="address-text">{inspection.address || '  '}</span>
                    </td>
                    <td>
                      <span className={`status ${getStatusClass(inspection.status)}`}>
                        {inspection.status}
                      </span>
                    </td>
                    <td>
                      <div className="property-type">
                        <span className="property-type-icon">
                          {getPropertyTypeIcon(inspection.property_type)}
                        </span>
                        <span className="property-type-text">{inspection.property_type}</span>
                      </div>
                    </td>
                    <td>
                      {inspection.photos_count > 0 ? (
                        <div className="photo-indicator">
                          <Camera size={16} />
                          <span className="photo-count">{inspection.photos_count}</span>
                        </div>
                      ) : (
                        <div className="no-photo">
                          <X size={16} />
                        </div>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            {(error ? getFilteredInspections : (data?.data?.inspections || getFilteredInspections)).length === 0 && (
              <div className="empty-state">
                <h3>  </h3>
                <p>      </p>
                <button 
                  className="btn btn-primary"
                  onClick={openCreateModal}
                >
                   
                </button>
              </div>
            )}

            {/*  */}
            {!error && data?.data.pagination && data.data.pagination.pages > 1 && (
              <div className="pagination">
                <button
                  className="btn btn-secondary"
                  disabled={page === 1}
                  onClick={() => setPage(page - 1)}
                >
                  
                </button>
                <span className="pagination-info">
                   {page}  {data.data.pagination.pages}
                </span>
                <button
                  className="btn btn-secondary"
                  disabled={page === data.data.pagination.pages}
                  onClick={() => setPage(page + 1)}
                >
                  
                </button>
              </div>
            )}
          </>
        )}
      </div>
      </div>

    </div>
  );
};

export default Inspections;
