import * as XLSX from 'xlsx';

interface InspectionForExport {
  id: number;
  internal_number?: string;
  status: string;
  address: string;
  inspector_name: string;
  recipient_name: string;
  created_at: string;
  sent_at?: string;
  property_type: string;
  object_type: string;
  object_description: string;
  photos_count: number;
  objects_count: number;
}

export const exportInspectionsToExcel = (inspections: InspectionForExport[], filename: string = '') => {
  //    
  const data = inspections.map(inspection => ({
    '': inspection.internal_number || inspection.id,
    '': inspection.status,
    '': new Date(inspection.created_at).toLocaleString('ru-RU'),
    '': inspection.sent_at ? new Date(inspection.sent_at).toLocaleString('ru-RU') : '-',
    '': inspection.inspector_name,
    '': inspection.recipient_name,
    ' ': inspection.property_type,
    ' ': inspection.object_type,
    '': inspection.object_description,
    '': inspection.address,
    '- ': inspection.objects_count,
    '- ': inspection.photos_count,
  }));

  //   
  const worksheet = XLSX.utils.json_to_sheet(data);
  const workbook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(workbook, worksheet, '');

  //   
  const columnWidths = [
    { wch: 15 }, // 
    { wch: 12 }, // 
    { wch: 18 }, // 
    { wch: 18 }, // 
    { wch: 25 }, // 
    { wch: 25 }, // 
    { wch: 20 }, //  
    { wch: 15 }, //  
    { wch: 30 }, // 
    { wch: 40 }, // 
    { wch: 12 }, // - 
    { wch: 12 }, // - 
  ];
  worksheet['!cols'] = columnWidths;

  //  
  const timestamp = new Date().toISOString().slice(0, 10);
  XLSX.writeFile(workbook, `${filename}_${timestamp}.xlsx`);
};

