export interface PropertyAttribute {
  key: string;
  label: string;
  type: 'text' | 'number' | 'date' | 'select';
  required: boolean;
  placeholder?: string;
  options?: string[];
  validation?: {
    min?: number;
    max?: number;
    pattern?: string;
    message?: string;
  };
}

export interface PropertyType {
  id: string;
  name: string;
  attributes: PropertyAttribute[];
}

